
.hero-container img {
  object-fit: cover; 
  width: 100%; 
  height: 100vh; 
  position: absolute; 
  top: 0;
  left: 0;
  z-index: -1; 
}


.hero-container {
  height: 100vh; 
  width: 100%; 
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative; 
  text-align: center;
  color: white;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.3); 
}


.hero-container > h1 {
  font-size: 100px;
  margin-top: -100px;
  z-index: 1; 
}


.hero-container > p {
  margin-top: 8px;
  font-size: 32px;
  font-family: 'Trebuchet MS', Arial, sans-serif;
  z-index: 1; 
}


.hero-btns {
  margin-top: 32px;
  z-index: 1;
}


@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -100px;
  }

  .hero-container > p {
    font-size: 30px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}

